@font-face {
    font-family: 'LINESeedKR-Bd';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_11-01@1.0/LINESeedKR-Rg.woff2') format('woff2');
    font-weight: 100;
    font-style: normal;
}

@font-face {
    font-family: 'KOTRAHOPE';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2110@1.0/KOTRAHOPE.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
}

body{
    margin: 0;
}

.header_main {
    background-color: #7895B2;
    width: 1200px;
    margin-left: auto;
    margin-right: auto;
    display: block;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}

.header_main_Logo > a > img {
    display: block;
    margin: auto;
}

.header_main_LogoAndInfo {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}

.header_main_LogoAndInfo > div {
    display: inline;
}

.header_main_ShopMenu > ul {
    margin-right: 10px;
    margin-top: 100px;
    padding-right: 0;
    list-style: none;
}

.header_main_ShopMenu > ul > li {
    float: left;
    margin-left: 20px;
    display: block;
}

.header_main_ShopMenu > ul > li:nth-of-type(2){
    margin-left: 0;
}

Nav {
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    background-color: #AEBDCA;
    margin-top: 20px;
    width: 1200px;
    list-style-type: none;
    text-align: center;
    padding: 0;
    justify-content: center;
    display: grid;
    grid-template-columns: repeat(5,1fr);
}

.NavLink:hover {
    color: #bad7e9;
    background-color: #2b3467;
    transition-property: color, background-color;
    transition-duration: 330ms;

}

.NavLink {
    padding-top: 10px;
    padding-bottom: 10px;
    color: #2b3467;
    text-decoration: none;
    font-size:20px;
    font-family: 'KOTRAHOPE';
    text-align: center;
    display: block;
}

.search_text{
    font-family: 'LINESeedKR-Bd';
    padding-left: 3px;
    font-size: 15px;
    width: 100px;
    height: 30px;
    border: solid black 1px;
    border-radius: 8px;
    background-color: #F5EFE6;
}

.search_button{
    cursor: pointer;
    width: 30px;
    height: 30px;
    border: none;
    background-color: transparent;
}